import './Form.css';
import { useState } from 'react'
import FirstStep from './Steps/FirstStep/FirstStep';
import SecondStep from './Steps/SecondStep/SecondStep';
import ThirdStep from './Steps/ThirdStep/ThirdStep';
import FourthStep from './Steps/FourthStep/FourthStep';
import Popup from './Popup/Popup';

function Form() {
  const [page, setPage] = useState(1);

  const [isPopupOpen, setIsPopupOpen] = useState(false);

  function closePopup() {
    setIsPopupOpen(false)
  }

  const buttonBackClassName = (
    `${page === 1 ? 'question-list__button_display_none' : 'question-list__button question-list__button_back'}`
  )

  const buttonsClassName = (
    `${page === 1 ? 'question-list__buttons question-list__buttons_step_first' : 'question-list__buttons'}`
  )

  function handleButtonNextClick() {
    setPage(page+1)
  };

  function handleButtonBackClick() {
    setPage(page-1)
  };

  function handleButtonOpenPopup() {
    setIsPopupOpen(true)
  }

  let step = null;

  if (page === 1) {
    step = <FirstStep />
  } else if (page === 2) {
    step = <SecondStep />
  } else if (page === 3) {
    step = <ThirdStep />
  } else if (page === 4) {
    step = <FourthStep />
  }

  return (
    <>
      <form className='question-list__form'>
        {step}
        <div className={buttonsClassName}>
          <button className={buttonBackClassName} onClick={handleButtonBackClick} type='button'>Назад</button>
          <button className='question-list__button question-list__button_next' onClick={page<4 ? handleButtonNextClick : handleButtonOpenPopup} type='button'>Далее</button>
        </div>
      </form>
      <Popup onClose={closePopup} isOpen={isPopupOpen}/>
    </>
  )
}

export default Form;