import './Filter.css';
import { useState } from 'react'
import Select from '../../../components/Select/Select';
import RadioFilter from '../../../components/RadioFilter/RadioFilter';
import RangeSlider from '../../../components/RangeSlider/RangeSlider';
import TextInput from '../../../components/TextInput/TextInput';
import useScreenWidth from "../../../utils/useScreenWidth";
import { selectOptions, RADIO_OPTION_GENDER } from '../../../constants/const'

function Filter({ filterClassName }) {
  const size =  useScreenWidth();

  const [isOpenAdvacedFilter, setIsOpenAdvacedFilter] = useState(false);
  const [isOpenBodyTipeFilter, setIsOpenBodyTipeFilter] = useState(false);
  const [isOpenRelationsFilter, setIsOpenRelationsFilter] = useState(false);

  function handleButtonViewMoreFiltersClick() {
    setIsOpenAdvacedFilter(!isOpenAdvacedFilter)
  };

  function handleButtonBodyTipeFiltersClick() {
    setIsOpenBodyTipeFilter(!isOpenBodyTipeFilter)
  };

  function handleButtonRelationsFiltersClick() {
    setIsOpenRelationsFilter(!isOpenRelationsFilter)
  };

  const advancedFilterClassName = (
    `${isOpenAdvacedFilter ? 'filter_advanced' : 'filter_advanced-invisible'}`
  )

  const buttonArrowAdvacedFilterClassName = (
    `${isOpenAdvacedFilter ? 'filter__button-arrow filter__button-arrow_filter-open' : 'filter__button-arrow'}`
  )

  const bodyTipeFilterClassName = (
    `${isOpenBodyTipeFilter ? 'filter__body-tipe' : 'filter__body-tipe_invisible'}`
  )

  const buttonArrowBodyTipeFilterClassName = (
    `${isOpenBodyTipeFilter ? 'filter__button-arrow filter__button-arrow_filter-open' : 'filter__button-arrow'}`
  )

  const relationsFilterClassName = (
    `${isOpenRelationsFilter ? 'filter__relations' : 'filter__relations_invisible'}`
  )

  const buttonArrowRelationsFilterClassName = (
    `${isOpenRelationsFilter ? 'filter__button-arrow filter__button-arrow_filter-open' : 'filter__button-arrow'}`
  )

  return (
    <form className={filterClassName}>
      <div className='filter_non-advanced'>
        <div className='filter__items'>
          <Select options={selectOptions} title={'Тематика'}/>
          <Select options={selectOptions} title={'Город'}/>
          <RadioFilter options={RADIO_OPTION_GENDER} title={'Пол'}/>
          <RangeSlider />
        </div>
        <div className={advancedFilterClassName}>
          {size.width > 788 ? 
          <>
          <div className='filter__items'>
            <Select options={selectOptions} title={'Тематика'}/>
            <Select options={selectOptions} title={'Город'}/>
            <RadioFilter options={RADIO_OPTION_GENDER} title={'Пол'}/>
            <RangeSlider options={RADIO_OPTION_GENDER}/>
          </div>
          <div className='filter__items filter__items_with-border'>
            <Select options={selectOptions} title={'Тематика'}/>
            <Select options={selectOptions} title={'Город'}/>
            <RadioFilter options={RADIO_OPTION_GENDER} title={'Пол'}/>
            <RangeSlider />
          </div>
          </>
          : '' }
          <div className='filter__container filter__container_body'>
            <button className='filter__container-button' onClick={handleButtonBodyTipeFiltersClick} type='button'>
              <p className='filter__container-title'>Телосложение</p>
              <span className={buttonArrowBodyTipeFilterClassName}></span>
            </button>
            <div className={bodyTipeFilterClassName}>
              <RadioFilter options={[RADIO_OPTION_GENDER[1], RADIO_OPTION_GENDER[2]]} title={'Пол'}/>
              <div className='filter__items'>
                <TextInput title={'Объем талии, см'} placeholder={'Введите значение'}/>
                <TextInput title={'Объем бедер, см'} placeholder={'Введите значение'}/>
                <Select options={selectOptions} title={'Телосложение'}/>
                <Select options={selectOptions} title={'Цвет кожи'}/>
              </div>
              <div className='filter__items'>
                <Select options={selectOptions} title={'Наличие волос на теле'}/>
                <Select options={selectOptions} title={'Внешность'}/>
                <Select options={selectOptions} title={'Телосложение'}/>
                <Select options={selectOptions} title={'Цвет кожи'}/>
              </div>
            </div>
          </div>
          <div className='filter__container filter__container_relations'>
            <button className='filter__container-button' onClick={handleButtonRelationsFiltersClick} type='button'>
              <p className='filter__container-title'>Отношения и интересы </p>
              <span className={buttonArrowRelationsFilterClassName}></span>
            </button>
            <div className={relationsFilterClassName}></div>
          </div>
        </div>
        <div className='filter__buttons'>
          <button className='filter__button_view-more' type='button' onClick={handleButtonViewMoreFiltersClick}>
            <p className='filter__button-text'>Расширенный фильтр</p>
            <div className={buttonArrowAdvacedFilterClassName}></div>
            </button>
          <div className='filter__buttons-container'>
            <button className='filter__button_reset' type='reset'>Сбросить</button>
            <button className='filter__button_submitt' type='submit'>Применить</button>
          </div>
        </div>
      </div>
    </form>
  );
}

export default Filter;