import { useState } from 'react';
import './App.css';
import Actors from './screens/Actors/Actors';
import About from './screens/About/About';
import Login from './screens/Login/Login';
import Register from './screens/Register/Register'
import { Route, Switch } from 'react-router-dom';
import { LastLocationProvider } from 'react-router-last-location';
import { ROUTES } from './constants/routes';
import QuestionList from './screens/QuestionList/QuestionList';
import ActorProfile from './screens/ActorProfile/ActorProfile';
import ProfileVerification from './screens/ProfileVerification/ProfileVerification';
import EditProfile from './screens/EditProfile/EditProfile';
import Reviews from './screens/Reviews/Reviews';
import Finance from './screens/Finance/Finance';
import ProfilePromotion from './screens/ProfilePromotion/ProfilePromotion'
import MyCastings from './screens/MyCastings/MyCastings';
import CastingProfile from './screens/CastingProfile/CastingProfile';
import Castings from './screens/Castings/Castings';

function App() {
  const [loggedIn, setLoggedIn] = useState(true);
  const [selectedCasting, setSelectedCasing] = useState({});

  function handleCastingClick(casting) {
    setSelectedCasing(casting)
  }

  return (
    <div className="page">
      <LastLocationProvider>
      <Switch>
        <Route exact path={ROUTES.ACTORS}>
          <Actors loggedIn={loggedIn}/>
        </Route>
        <Route exact path={ROUTES.CASTINGS}>
          <Castings loggedIn={loggedIn}/>
        </Route>
        <Route exact path={ROUTES.ABOUT}>
          <About loggedIn={loggedIn}/>
        </Route>
        <Route path={ROUTES.LOGIN}>
          <Login />
        </Route>
        <Route path={ROUTES.REGISTER}>
          <Register />
        </Route>
        <Route path={ROUTES.QUESTION_LIST}>
          <QuestionList loggedIn={loggedIn}/>
        </Route>
        <Route path={ROUTES.ACTOR_PROFILE}>
          <ActorProfile loggedIn={loggedIn}/>
        </Route>
        <Route path={ROUTES.EDIT_PROFILE}>
          <EditProfile loggedIn={loggedIn}/>
        </Route>
        <Route path={ROUTES.PROFILE_VERIFICATION}>
          <ProfileVerification loggedIn={loggedIn}/>
        </Route>
        <Route path={ROUTES.REVIEWS}>
          <Reviews loggedIn={loggedIn}/>
        </Route>
        <Route path={ROUTES.FINANCE}>
          <Finance loggedIn={loggedIn}/>
        </Route>
        <Route path={ROUTES.PROFILE_PROMOTION}>
          <ProfilePromotion loggedIn={loggedIn}/>
        </Route>
        <Route path={ROUTES.MY_CASTINGS}>
          <MyCastings loggedIn={loggedIn} onCastingClick={handleCastingClick}/>
        </Route>
        <Route path={ROUTES.CASTING_PROFILE}>
          <CastingProfile loggedIn={loggedIn} casting={selectedCasting}/>
        </Route>
      </Switch>
      </LastLocationProvider>
    </div>
  );
}

export default App;
