import './AddMoney.css';
import TextInput from '../../../components/TextInput/TextInput';
import RadioFilter from '../../../components/RadioFilter/RadioFilter';
import Popup from './Popup/Popup';
import { useState } from 'react'

function AddMoney() {
  const radioOptions = [
    {
      id: 1,
      value: 'card',
      label: 'Карта',
    },
    {
      id: 2,
      value: 'yoomoney',
      label: 'Юмани',
    },
  ];
  
  const [isPopupOpen, setIsPopupOpen] = useState(false);

  function closePopup() {
    setIsPopupOpen(false)
  }

  function handleButtonOpenPopup(e) {
    setIsPopupOpen(true)
  }

  return (
    <>
      <form className='tabs__form add-money'>
        <p className='add-money__ratio'><span className='add-money__ratio-icon'/>Курс: 1 каст = 1 рубль</p>
        <div className='add-money__container'>
          <TextInput title={'Сумма пополнения'} placeholder={'Введите сумму'} inputClassName={'input__title_limited_width'}/>
          <RadioFilter options={radioOptions} title={'Способ оплаты'}/>
        </div>
        <div className='add-money__card-data'>
          <input className='add-money__input add-money__input_card-number' placeholder='Номер карты'/>
          <input className='add-money__input add-money__input_owner' placeholder='Владелец'/>
          <input className='add-money__input add-money__input_date' placeholder='00/00'/>
          <input className='add-money__input add-money__input_cvc' placeholder='CVC'/>
        </div>
        <button type='button' className='add-money__button' onClick={handleButtonOpenPopup}>Пополнить</button>
      </form>
      <Popup onClose={closePopup} isOpen={isPopupOpen}/>
    </>
  )
}

export default AddMoney;