import './WithdrawFunds.css';
import TextInput from '../../../components/TextInput/TextInput';
import RadioFilter from '../../../components/RadioFilter/RadioFilter';
import { useState } from 'react';

function WithdrawFunds() {
  const radioOptions = [
    {
      id: 1,
      value: 'card',
      label: 'Карта',
    },
    {
      id: 2,
      value: 'bank-account',
      label: 'Банковский счет',
    },
  ];

  const [sum, setSum] = useState();
  const [focus, setFocus] = useState(false);
  const [toggleValue, setToggleValue] = useState('card');
  
  function handleSumChange(e) {
    setSum(e.target.value)
  };

  function handleSumFocus() {
    setFocus(true)
  };

  function handleSumBlur() {
    setFocus(false)
  };

  function  toggleCheckbox(e) {
    setToggleValue(e)
  }

  const commissionValue = () => {
    if(typeof(sum) === 'undefined') {
      return 0
    } else {
      return `${sum * 5 /100}`
    }
  }
  
  const commissionClassName = (
    `${focus ? 'withdraw__commission' : 'withdraw__commission_invisible'}`
  );

  return (
    <form className='withdraw tabs__form'>
      <div className='withdraw__info-container'>
        <h2 className='withdraw__title'>Вывод средств</h2>
        <p className='withdraw__subtitle'>Минимальная сумма вывода составляет 1000 кастов + 5% комиссия сервиса. Сам банк также может назначать комиссию за перевод.</p>
        <p className='withdraw__ratio'><span className='withdraw__ratio-icon'/>Курс: 1 каст = 1 рубль</p>
      </div>
      <div className='withdraw__sum-container'>
        <label className='input__title'>
          Сумма вывода
          <input className='input' placeholder='Введите сумму' onChange={handleSumChange} onFocus={handleSumFocus} onBlur={handleSumBlur} type='number'></input>
        </label>
        <p className={commissionClassName}>Комиссия составит: {commissionValue()} рублей</p>
        <RadioFilter options={radioOptions} title={'Куда вывести'} onChange={toggleCheckbox} toggleValue={toggleValue}/>
      </div>
        {
          toggleValue === 'card' ?
          <div className='withdraw__data-container'>
            <TextInput title={'Номер карты'} placeholder={'Введите номер'} inputClassName={'input__title_limited_width'}/>
            <TextInput title={'Владелец каты'} placeholder={'Как на карте'} inputClassName={'input__title_limited_width'}/>
          </div>
          :
          <div className='withdraw__data-container'>
            <TextInput title={'Номер счета'} placeholder={'Номер вашего счета'} />
            <TextInput title={'Банк'} placeholder={'Название банка'} />
            <TextInput title={'БИК'} placeholder={'БИК банка'} />
          </div>
        }
      <button className='withdraw__button' type='submit'>Вывести</button>
    </form>
  )
}

export default WithdrawFunds;