import { useState } from 'react';
import './RangeSlider.css';
import Slider from '@mui/material/Slider';


function RangeSlider() {
  const [minAge, setMinAge] = useState(18);
  const [maxAge, setMaxAge] = useState(55);

  const handleSliderChange = (event, newValue) => {
    setMinAge(newValue[0]);
    setMaxAge(newValue[1]);
  };

  const handleInputMinAge = (event) => {
    setMinAge(event.target.value === '' ? '' : Number(event.target.value));
  };

  const handleInputMaxAge = (event) => {
    setMaxAge(event.target.value === '' ? '' : Number(event.target.value));
  };

  const handleBlurMinAge = () => {
    if (minAge< 0 ) {
      setMinAge(0);
    } else if (minAge > 100) {
      setMinAge(100);
    }
  };

  const handleBlurMaxAge = () => {
    if (maxAge< 0) {
      setMaxAge(0);
    } else if (maxAge > 100) {
      setMaxAge(100);
    }
  };
  return (
    <div className='range-slider'>
      <h3 className='range-slider__title'>Возраст</h3>
      <div className='range-slider__container'>
        <input
          className='range-slider__input'
          value={minAge}
          onChange={handleInputMinAge}
          onBlur={handleBlurMinAge}
          type='number'
        />
        <Slider
          value={[minAge, maxAge]}
          onChange={handleSliderChange}
        />
        <input
          className='range-slider__input'
          onChange={handleInputMaxAge}
          value={maxAge}
          onBlur={handleBlurMaxAge}
          type='number'
        />
      </div>
    </div>
  );
}

export default RangeSlider;