import './RadioFilter.css';
import Option from './Option/Option';
import { useLocation } from 'react-router-dom';

function RadioFilter({ options, title, onChange, toggleValue }) {
  const location = useLocation();

  return (
    <div className='filter-radio'>
      <h3 className={`filter-radio__title ${location.pathname === '/signup' && 'filter-radio__title_display_none'}`}>{title}</h3>
      <div className={`filter-radio__container ${location.pathname === '/signup' && 'filter-radio__container_component_register'}`}>
        {options.slice(0, options.length).map((option) =>
        (<Option option={option} key={option.id} location={location.pathname} onChange={onChange} toggleValue={toggleValue}/>)
        )}
    </div>
  </div>
  );
}

export default RadioFilter;