import './EditProfile.css';
import { useState } from 'react';
import Footer from '../../components/Footer/Footer';
import Header from '../../components/Header/Header';
import GeneralInfoInputs from '../../components/GeneralInfoInputs/GeneralInfoInputs';
import BodyInfoInputs from '../../components/BodyInfoInputs/BodyInfoInputs';
import InterestsInputs from '../../components/InterestsInputs/InterestsInputs';
import PhotoInputs from '../../components/PhotoInputs/PhotoInputs';
import ThemeList from '../../components/ThemeList/ThemeList';
import { THEME_OPTIONS } from '../../constants/const';

function EditProfile({ loggedIn }) {
  const [isOpenGeneralInfoFilter, setIsOpenGeneralInfoFilter] = useState(true);
  const [isOpenBodyFilter, setIsOpenBodyFilter] = useState(false);
  const [isOpenInterestsFilter, setIsOpenInterestsFilter] = useState(false);
  const [isOpenPhotoFilter, setIsOpenPhotoFilter] = useState(false);
  const [isOpenThemeFilter, setIsOpenThemeFilter] = useState(true);

  function handleButtonGeneralInfoFilterClick() {
    setIsOpenGeneralInfoFilter(!isOpenGeneralInfoFilter)
  };

  function handleButtonBodyFilterClick() {
    setIsOpenBodyFilter(!isOpenBodyFilter)
  };

  function handleButtonInterestsFilterClick() {
    setIsOpenInterestsFilter(!isOpenInterestsFilter)
  };

  function handleButtonPhotoFilterClick() {
    setIsOpenPhotoFilter(!isOpenPhotoFilter)
  };

  function handleButtonThemeFilterClick() {
    setIsOpenThemeFilter(!isOpenThemeFilter)
  };

  const generalInfoFilterClassName = (
    `${isOpenGeneralInfoFilter ? 'questionary__questions' : 'questionary__questions_invisible'}`
  )

  const bodyFilterClassName = (
    `${isOpenBodyFilter ? '' : 'questionary__questions_invisible'}`
  )

  const interestsFilterClassName = (
    `${isOpenInterestsFilter ? '' : 'questionary__questions_invisible'}`
  )

  const photoFilterClassName = (
    `${isOpenPhotoFilter ? '' : 'questionary__questions_invisible'}`
  )

  const themeFilterClassName = (
    `${isOpenThemeFilter ? '' : 'questionary__questions_invisible'}`
  )

  return (
    <>
      <Header loggedIn={loggedIn}/>
      <main className='content content_edit-profile edit-profile'>
        <h1 className='edit-profile__title'>Редактирование профиля</h1>
        <form className='edit-profile__form'>
          <div className='edit-profile__container'>
            <button className={`edit-profile__button ${!isOpenGeneralInfoFilter && 'edit-profile__button_filter-open'}`} type='button' onClick={handleButtonGeneralInfoFilterClick}>
              <p className='edit-profile__subtitle'>Общие данные</p>
              <span className={`edit-profile__button-arrow ${isOpenGeneralInfoFilter && 'edit-profile__button-arrow_filter-open'}`}/>
            </button>
            <GeneralInfoInputs generalInfoClassName={generalInfoFilterClassName}/>
          </div>
          <div className='edit-profile__container'>
            <button className={`edit-profile__button ${!isOpenBodyFilter && 'edit-profile__button_filter-open'}`} type='button' onClick={handleButtonBodyFilterClick}>
              <p className='edit-profile__subtitle'>Телосложение</p>
              <span className={`edit-profile__button-arrow ${isOpenBodyFilter && 'edit-profile__button-arrow_filter-open'}`}/>
            </button>
            <BodyInfoInputs bodyFilterClassName={bodyFilterClassName}/>
          </div>
          <div className='edit-profile__container'>
            <button className={`edit-profile__button ${!isOpenInterestsFilter && 'edit-profile__button_filter-open'}`} type='button' onClick={handleButtonInterestsFilterClick}>
              <p className='edit-profile__subtitle'>Отношения и интересы</p>
              <span className={`edit-profile__button-arrow ${isOpenInterestsFilter && 'edit-profile__button-arrow_filter-open'}`}/>
            </button>
            <InterestsInputs interestsFilterClassName={interestsFilterClassName}/>
          </div>
          <div className='edit-profile__container'>
            <button className={`edit-profile__button ${!isOpenPhotoFilter && 'edit-profile__button_filter-open'}`} type='button' onClick={handleButtonPhotoFilterClick}>
              <p className='edit-profile__subtitle'>Фото и видео</p>
              <span className={`edit-profile__button-arrow ${isOpenPhotoFilter && 'edit-profile__button-arrow_filter-open'}`}/>
            </button>
            <PhotoInputs photoFilterClassName={photoFilterClassName}/>
          </div>
          <button className={`edit-profile__button ${!isOpenThemeFilter && 'edit-profile__button_filter-open'}`} type='button' onClick={handleButtonThemeFilterClick}>
            <p className='edit-profile__subtitle'>Направление и профиль</p>
            <span className={`edit-profile__button-arrow ${isOpenThemeFilter && 'edit-profile__button-arrow_filter-open'}`}/>
          </button>
          <div className={themeFilterClassName}>
            <p className='edit-profile__choose-theme'>Выберите тематику разделов</p>
            <ThemeList options={THEME_OPTIONS}/>
            <p className='edit-profile__choose-type'>Выберите тип профиля</p>
            <div className='edit-profile__radio-input'>
              <label className='edit-profile__label'>
                <input type='radio' name='radio' value='unprivileged' className='edit-profile__input edit-profile__input_invisible' />
                <div className='edit-profile__input_visible'>
                  <h3 className='edit-profile__input-title'>Обычный</h3>
                  <ul  className='edit-profile__input-advantages'>
                    <li className='edit-profile__input-advantage'>
                      <span className='edit-profile__input-icon'/>
                      <p className='edit-profile__input-text'>Бесплатный находится в общем списке по дате регистрации, Заказчики видят информацию анкеты и только фотки в свободной тематики 5 штук.</p>
                    </li>
                  </ul>
                  <div className='edit-profile__input-price'>
                    <p className='edit-profile__input-price-value'>Бесплатно</p>
                  </div>
                </div>
              </label>
              <label className='edit-profile__label'>
                <input type='radio' name='radio' value='privileged' className='edit-profile__input edit-profile__input_invisible' />
                <div className='edit-profile__input_visible'>
                  <h3 className='edit-profile__input-title'>Проверенный<span className='edit-profile__input-tick'/></h3>
                  <ul  className='edit-profile__input-advantages'>
                    <li className='edit-profile__input-advantage'>
                      <span className='edit-profile__input-icon'/>
                      <p className='edit-profile__input-text'>В профиле появляется значок «Верифицирован».</p>
                    </li>
                    <li className='edit-profile__input-advantage'>
                      <span className='edit-profile__input-icon'/>
                      <p className='edit-profile__input-text'>Можно предлагать свою кандидатуру в раздел 18+ и работа за границей</p>
                    </li>
                    <li className='edit-profile__input-advantage'>
                      <span className='edit-profile__input-icon'/>
                      <p className='edit-profile__input-text'>Можно воспользоваться платной услугой для продвижения своего профиля в ленте на главной странице сайта. Первые 24 часа даются в подарок.</p>
                    </li>
                  </ul>
                  <div className='edit-profile__input-price'>
                    <p className='edit-profile__input-price-value'>От 100 каст</p>
                    <p className='edit-profile__input-rate'>1 каст = 1 рубль</p>
                  </div>
                </div>
              </label>
            </div>
          </div>
          <button className='edit-profile__save-button' type='submit'>Сохранить</button>
        </form>
      </main>
      <Footer />
    </>
  )
}

export default EditProfile;