import './SliderSection.css';
import {useEffect, useRef, useState} from 'react';
import React, { Component } from "react";

import Slider from "react-slick";
import sliderPhotoFirs from '../../../../images/Photo/slider-photo1.png';
import sliderPhotoSecond from '../../../../images/Photo/slider-photo2.png';
import sliderPhotoThird from '../../../../images/Photo/slider-photo3.png';
import sliderPhotoFourth from '../../../../images/Photo/slider-photo4.png';

import "slick-carousel/slick/slick.css"; 
import "slick-carousel/slick/slick-theme.css";
import SliderItem from './SliderItem/SliderItem';

const photos =[
  {
    id: 1,
    src: sliderPhotoFirs
  },
  {
    id: 2,
    src: sliderPhotoSecond
  },
  {
    id: 3,
    src: sliderPhotoThird
  },
  {
    id: 4,
    src: sliderPhotoFourth
  },
  {
    id: 5,
    src: sliderPhotoFirs
  },
  {
    id: 6,
    src: sliderPhotoSecond
  },
  {
    id: 7,
    src: sliderPhotoThird
  },
  {
    id: 8,
    src: sliderPhotoFourth
  }
];

function SampleNextArrow(props) {
  const { className, style, onClick } = props;
  return (
    <div
      className={className}
      style={{ ...style}}
      onClick={onClick}
    />
  );
}

function SamplePrevArrow(props) {
  const { className, style, onClick } = props;
  return (
    <div
      className={className}
      style={{ ...style }}
      onClick={onClick}
    />
  );
}

export default class SliderSection extends Component {


  render() {
    
    this.myRef = React.createRef();
    const settings = {
      dots: false,
      infinite: true,
      speed: 500,
      slidesToShow: 4,
      slidesToScroll: 1,
      nextArrow: <SampleNextArrow />,
      prevArrow: <SamplePrevArrow />
    };
    
    return (
      <div>
        <Slider {...settings} >
          {photos.map((photo, index) =>
            <SliderItem photo={photo} key={photo.id}/>
          )}
        </Slider>
      </div>
    );
  }
}